/* ============= GLOBAL SECTION SPACING ============= */
section {
  margin-bottom: 90px;
}

/* ============= HERO ============= */
.clean-hero {
  position: relative;
  background: linear-gradient(180deg, #ffffff, #f4f7ff);
  padding: 110px 20px 90px;
  overflow: hidden;
}

/* Animated background blobs */
.clean-hero::before,
.clean-hero::after {
  content: "";
  position: absolute;
  width: 420px;
  height: 420px;
  background: radial-gradient(circle, rgba(249,115,22,0.18), transparent 70%);
  border-radius: 50%;
  animation: float 12s infinite ease-in-out;
  z-index: 0;
}

.clean-hero::before {
  top: -120px;
  left: -120px;
}

.clean-hero::after {
  bottom: -140px;
  right: -140px;
  animation-delay: 6s;
}

@keyframes float {
  0% { transform: translateY(0px); }
  50% { transform: translateY(25px); }
  100% { transform: translateY(0px); }
}

.clean-hero-inner {
  max-width: 1200px;
  margin: auto;
  position: relative;
  z-index: 2;
}

/* Badge */
.hero-badge {
  display: inline-block;
  background: #ecfdf3;
  color: #166534;
  padding: 7px 16px;
  border-radius: 999px;
  font-size: 13px;
  margin-bottom: 18px;
}

/* Headings */
.clean-hero h1 {
  font-size: 44px;
  font-weight: 800;
  color: #111827;
  line-height: 1.15;
}

.clean-hero h1 span {
  color: #f97316;
}

.clean-hero p {
  margin-top: 18px;
  color: #4b5563;
  max-width: 620px;
  line-height: 1.8;
}

/* Buttons */
.hero-buttons {
  margin-top: 36px;
  display: flex;
  gap: 18px;
}

.primary-btn {
  background: #f97316;
  color: white;
  border-radius: 999px;
  padding: 14px 34px;
  border: none;
  font-weight: 600;
  transition: 0.3s;
}

.primary-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 28px rgba(249,115,22,0.3);
}

.outline-btn {
  background: white;
  border: 1px solid #e5e7eb;
  border-radius: 999px;
  padding: 14px 34px;
  font-weight: 600;
  transition: 0.2s;
}

.outline-btn:hover {
  background: #f9fafb;
}

.hero-benefits {
  margin-top: 26px;
  display: flex;
  gap: 30px;
  font-size: 14px;
  color: #374151;
}

/* ============= METRICS ============= */
.metrics-section {
  display: flex;
  justify-content: center;
  gap: 120px;
  padding: 70px 20px;
  background: white;
}

.metric-card h2 {
  font-size: 38px;
}

/* ============= WHY SECTION ============= */
.why-clean {
  display: grid;
  grid-template-columns: 1.2fr 1fr;
  gap: 70px;
  max-width: 1200px;
  margin: auto;
  padding: 100px 20px;
}

.why-clean h2 {
  margin-bottom: 14px;
}

.why-clean ul {
  margin-top: 20px;
  line-height: 2;
}

.why-right {
  display: grid;
  gap: 18px;
}

.why-box {
  background: #ffffff;
  padding: 20px;
  border-radius: 14px;
  border: 1px solid #e5e7eb;
  transition: 0.25s;
}

.why-box:hover {
  transform: translateY(-4px);
  box-shadow: 0 12px 28px rgba(0,0,0,0.1);
}

/* ============= SERVICES ============= */
.services-clean {
  background: #f5f7fb;
  padding: 100px 20px;
  text-align: center;
}

.services-clean h2 {
  margin-bottom: 50px;
}

.services-grid {
  max-width: 1200px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
  gap: 28px;
}

.service-card {
  background: white;
  padding: 36px 26px;
  border-radius: 18px;
  border: 1px solid #e5e7eb;
  cursor: pointer;
  transition: 0.25s;
}

.service-card:hover {
  transform: translateY(-6px);
  box-shadow: 0 24px 40px rgba(0,0,0,0.12);
}

/* ============= BUSINESS STAGES ============= */
.business-stage {
  padding: 100px 20px;
  text-align: center;
}

.stage-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  gap: 26px;
  margin-top: 38px;
}

.stage-card {
  padding: 28px;
  border-radius: 18px;
  font-weight: 600;
  transition: 0.25s;
}

.stage-card:hover {
  transform: translateY(-6px);
}

.blue { background: #e0f2fe; }
.yellow { background: #fef3c7; }
.red { background: #fee2e2; }
.green { background: #dcfce7; }

/* ============= FINAL CTA ============= */
.final-clean-cta {
  background: linear-gradient(135deg, #1d4ed8, #4f46e5);
  color: white;
  text-align: center;
  padding: 110px 20px;
}

.final-clean-cta h2 {
  margin-bottom: 14px;
}

.final-clean-cta button {
  margin-top: 24px;
  background: #f97316;
  color: white;
  border-radius: 999px;
  padding: 14px 44px;
  border: none;
  font-weight: 600;
  transition: 0.3s;
}

.final-clean-cta button:hover {
  transform: translateY(-2px);
  box-shadow: 0 12px 30px rgba(249,115,22,0.45);
}

/* ============= MOBILE FIXES ============= */
@media (max-width: 768px) {
  .metrics-section {
    flex-direction: column;
    gap: 40px;
  }

  .why-clean {
    grid-template-columns: 1fr;
    gap: 50px;
    padding: 70px 20px;
  }

  .hero-buttons {
    flex-direction: column;
  }
}
