.clean-header {
  position: sticky;
  top: 0;
  z-index: 999;
  background: #ffffff;
  border-bottom: 1px solid #e5e7eb;
}

.clean-header-inner {
  max-width: 1280px;
  margin: auto;
  padding: 14px 24px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

/* LOGO */
.clean-logo {
  font-size: 20px;
  font-weight: 700;
  cursor: pointer;
  color: #111827;
}

/* NAV */
.clean-nav {
  display: flex;
  gap: 28px;
  font-size: 14px;
  font-weight: 500;
}

.clean-nav span {
  cursor: pointer;
  color: #374151;
}

.clean-nav span:hover {
  color: #f97316;
}

/* ACTIONS */
.clean-actions {
  display: flex;
  align-items: center;
  gap: 14px;
}

/* BUTTONS */
.clean-login {
  background: transparent;
  border: none;
  font-weight: 500;
  cursor: pointer;
  color: #374151;
}

.clean-primary {
  background: #f97316;
  color: white;
  border: none;
  padding: 6px 16px;
  border-radius: 999px;
  font-weight: 600;
  cursor: pointer;
}

/* PROFILE */
.clean-profile-dropdown {
  display: flex;
  align-items: center;
  gap: 8px;
  cursor: pointer;
}

.clean-avatar {
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background: #f97316;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
  font-weight: bold;
}

.profile-name {
  font-size: 14px;
  color: #374151;
}
